.navMini {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: fixed;
  background-color: transparent;
  width: 5rem;
  height: 10rem;
  top: 80%;
  left: 2%;
  z-index: 2;
  scroll-behavior: smooth;
  transition-duration: 2s;
}

.navMiniHide {
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: fixed;
  background-color: transparent;
  width: 5rem;
  height: 10rem;
  top: 80%;
  left: -10%;
  z-index: 2;
  scroll-behavior: smooth;
  transition-duration: 2s;
}

.navLink {
  border: 1px solid white;
  height: 1px;
  width: 2rem;
  border-radius: 10px;
  background-color: white;
  padding: 1px;
  transition-duration: 1.5s;
  transition-delay: .5s;
}

.navLinkActive {
  border: 1px solid white;
  height: 1px;
  width: 5rem;
  border-radius: 10px;
  background-color: white;
  padding: 1px;
  transition-duration: 1.5s;
  transition-delay: .5s;
}

.navCont {
  display: flex;
  align-items: center;
  height: 2rem;
  margin: 0;
}

.navCont:hover .navLink {
  box-shadow: 0px 0px 5px white;
}

.navCont:focus .navLink {
  width: 5rem;
}

@media screen and (max-width: 400px) {
  .navMini {
    display: none;
  }
}